import { Component, Input } from '@angular/core';
import {
  ActingErrorMessage,
  ActingErrorMessages
} from '@sofico-framework/utils';
import { Observable } from 'rxjs';

@Component({
  selector: 'sof-acting-error-messages',
  template: `
    <sof-alert type="danger" *ngFor="let err of actingErrorMessages$ | async">
      {{
        err?.translation
          ? err?.translation
          : (tc + '.' + err?.message | translate: err.messageParams)
      }}
    </sof-alert>
  `,
  styleUrls: ['./acting-error-messages.component.scss']
})
export class ActingErrorMessagesComponent {
  @Input() tc: string;
  @ActingErrorMessages() actingErrorMessages$: Observable<ActingErrorMessage[]>;
}
